body {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 14px;
  line-height: 21px;
  background-color: #333333;
  color: #ffffff;
  margin: 0;
  padding: 0;
}

.gold-text {
  color: #d1a954;
}

.container {
  margin: 0 auto;
  padding: 0 25px;
}
@media (min-width: 768px) {
  .container {
    padding: 0 60px;
  }
}
@media (min-width: 992px) {
  .container {
    padding: 0 100px;
  }
}
@media (min-width: 1360px) {
  .container {
    padding: 0 200px;
  }
}

.main-header {
  background-image: url("../img/bg-header.png");
  background-repeat: no-repeat;
  background-size: 768px 530px;
  padding: 40px 25px 80px 25px;
}
.main-header a {
  text-decoration: none;
  color: #ffffff;
  transition: 0.4s;
}
.main-header a:hover, .main-header a:focus {
  opacity: 0.6;
  transition: 0.4s;
}

.main-header__info-block {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.main-header__main-title {
  font-size: 35px;
  letter-spacing: 4px;
  text-transform: uppercase;
  margin-bottom: 50px;
}

.main-header__main-info {
  width: 310px;
  margin: 0;
}

.main-header__poster-link {
  display: inline-flex;
  font-size: 18px;
  text-transform: uppercase;
  justify-content: center;
  align-items: center;
  width: 160px;
  height: 50px;
  background-color: #d1a954;
  margin-top: 50px;
  margin-right: auto;
}

.main-nav {
  display: flex;
}

.main-nav__nav-list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.main-nav__link {
  height: 100%;
  border-bottom: 2px solid #d1a954;
  margin-left: auto;
}

.about {
  padding-top: 80px;
}
.about h3 {
  text-transform: uppercase;
  letter-spacing: 1px;
  text-align: center;
  margin-top: 0;
  margin-bottom: 40px;
}

.about__title {
  font-size: 30px;
  font-weight: 300;
  text-transform: uppercase;
  letter-spacing: 3px;
  text-align: center;
  margin-top: 0;
  margin-bottom: 60px;
}

.about__our-info {
  text-align: center;
  margin-top: 0;
  margin-bottom: 50px;
}

.about__info-block {
  display: flex;
  justify-content: space-around;
}
.about__info-block p {
  position: relative;
  margin: 0;
  padding-top: 80px;
}
.about__info-block p:nth-of-type(1)::before {
  content: "";
  position: absolute;
  bottom: 35px;
  left: 0;
  width: 70px;
  height: 70px;
  background-image: url("../img/armchair.svg");
  background-repeat: no-repeat;
}
.about__info-block p:nth-of-type(2)::before {
  content: "";
  position: absolute;
  bottom: 35px;
  left: 0;
  width: 70px;
  height: 70px;
  background-image: url("../img/theater.svg");
  background-repeat: no-repeat;
}

.performances {
  padding-top: 80px;
}

.performances__title {
  font-size: 30px;
  font-weight: 300;
  text-transform: uppercase;
  letter-spacing: 3px;
  text-align: center;
  margin-top: 0;
  margin-bottom: 60px;
}

.performances__main-block {
  height: 350px;
}

.performances__list {
  width: 310px;
  margin: 0 auto;
  padding: 0;
  list-style: none;
}

.performances__item a {
  display: block;
  width: 310px;
  height: 350px;
}
.performances__item:nth-of-type(1) a {
  background-image: url("../img/project-1.png");
  background-repeat: no-repeat;
  background-size: 310px 350px;
}

.performances__slider-list {
  display: flex;
  column-gap: 10px;
  width: 70px;
  margin: 10px auto 40px auto;
  padding: 0;
  list-style: none;
}

.performances__slider-button {
  width: 10px;
  height: 10px;
  padding: 0;
  border: none;
  border-radius: 50%;
  background-color: #989898;
  cursor: pointer;
  transition: 0.4s;
}
.performances__slider-button:hover, .performances__slider-button:focus {
  opacity: 0.6;
  transition: 0.4s;
}
.performances__slider-button--active {
  border: 2px solid #ffffff;
}

.rating {
  display: flex;
  justify-content: center;
}

.partners {
  padding-top: 80px;
}

.partners__title {
  font-size: 30px;
  font-weight: 300;
  text-transform: uppercase;
  letter-spacing: 3px;
  text-align: center;
  margin-top: 0;
  margin-bottom: 60px;
}

.partners__main-block {
  display: flex;
  justify-content: center;
}

.partners__list {
  display: grid;
  grid-template-columns: min-content min-content;
  gap: 10px;
  margin: 0;
  padding: 0;
  list-style: none;
}

.partners__item {
  width: 150px;
  height: 70px;
  background-image: url("../img/gazprom_logo.svg");
  background-repeat: no-repeat;
}

.donate {
  padding-top: 80px;
}

.donate__title {
  font-size: 30px;
  font-weight: 300;
  text-transform: uppercase;
  letter-spacing: 3px;
  text-align: center;
  margin-top: 0;
  margin-bottom: 60px;
}

.donate__form {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.donate__form fieldset {
  display: grid;
  grid-template-columns: 147px 147px;
  grid-template-rows: min-content;
  gap: 10px;
  align-self: center;
  margin: 0;
  padding: 0;
  border: none;
}
.donate__form input {
  width: 135px;
  background-color: #444444;
  color: #ffffff;
  padding: 10px 0 10px 10px;
  border: 1px solid #616161;
}
.donate__form textarea {
  width: 292px;
  height: 80px;
  background-color: #444444;
  color: #ffffff;
  margin: 0;
  padding: 10px 0 10px 10px;
  border: 1px solid #616161;
  resize: none;
}

.donate__button {
  font-weight: 700;
  text-transform: uppercase;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  align-self: center;
  width: 190px;
  height: 50px;
  background-color: #d1a954;
  color: #ffffff;
  margin: 40px 0 0 0;
  padding: 0;
  border: none;
  cursor: pointer;
  transition: 0.4s;
}
.donate__button:hover, .donate__button:focus {
  opacity: 0.6;
  transition: 0.4s;
}

.donate__confidentiality {
  text-align: center;
  color: #989898;
  margin-top: 30px;
  margin-bottom: 0;
}

.main-footer__contact-block {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 50px;
}
.main-footer__contact-block p {
  font-weight: 700;
  text-transform: uppercase;
  margin: 0;
}
.main-footer__contact-block a {
  text-decoration: none;
  color: #ffffff;
  transition: 0.4s;
}
.main-footer__contact-block a:hover, .main-footer__contact-block a:focus {
  opacity: 0.6;
  transition: 0.4s;
}

.main-footer__adress-block {
  margin-top: 15px;
  text-align: center;
}

.main-footer__copyright-block {
  text-align: center;
  color: #989898;
  border-top: 1px solid #989898;
  margin-top: 30px;
  padding-bottom: 20px;
}
.main-footer__copyright-block p {
  margin-bottom: 0;
}